<template>
	<div class="search-box">
		<van-search v-model="keywords" :placeholder="placeholder"  input-align="center"/>
	</div>

</template>

<script>
import {Search } from 'vant'

export default {
  name: 'SearchBox',
  components:{
	[Search .name]:Search 
  },
  data(){
	return{
		keywords:'',			//关键字
		prevKeywords:'',		//保存上一个输入的关键字
		n:0,
		placeholder:'请输入关键词',
	}
  },
  methods:{

	//发送数据
	searchclick(){
		//如果关键字没有重新输入就不发送
		if(this.keywords==this.prevKeywords){
			return
		}
		this.$emit('searchclick',this.keywords)
		//防止在发送
		this.prevKeywords=this.keywords
	},
  },
	//监听属性发生修改
	watch:{
		//关键字属性发生修改的时候
		keywords(){
			//如果关键字没有重新输入就不发送
			this.$emit('changeKeys',this.keywords)
		}
	}

}
</script>

<style scoped>
*{
	margin:0;
	padding:0;
}
.search-box{
	padding:3px 8px;
}
</style>
